.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 45px 0 40px;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 539px) {
    .header {
        padding: 28px 27px 28px;
        box-sizing: border-box;
    }
}