.profile {
    display: grid;
    grid-template-columns: max-content minmax(200px, 500px) 1fr;
    grid-auto-flow: column;
}

@media screen and (max-width: 899px) {
    .profile {
        grid-template-columns: minmax(200px, 1fr);
        grid-auto-flow: row;
        justify-items: center;
    }
}